<template>
  <div class="homemiddle">
    <div class="Sentiment">
      <span><van-icon name="star" /></span>
      <span>{{message}}</span>
    </div>
    <div>
      <ul class="moneylist" >
        <li v-for="(item, index) in moneylist" 
            :key="item"
            @click="moneyclick(index)">
              <span>￥</span>
              {{item}}
        </li>
      </ul>
    </div>
    <div class="othermoney">
      <div>其他金额：</div>
      <div><input id="inputmoney" type="text"  @blur="inputchange"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "homemiddle",
    data() {
      return {
        money:0,
        moneylist: [10,33,66,88,108,168,188,208],
        message: "十力无畏，事事如意！",
        messagelist: ['十力无畏，事事如意！',
                      '万事顺风顺水！',
                      '六根清澈，无诸恼患！',
                      '八千为春，八千为秋！',
                      '断百八烦恼，身心寂静！',
                      '舍三途苦，灭诸烦恼，开彼智慧！',
                      '八方向化八分和！',
                      '一心念佛，六时吉祥，八德常享！']
      }
    },
    methods: {
      moneyclick(index,item){
        this.message = this.messagelist[index]
        this.money = this.moneylist[index]
        const inputmoney = document.getElementById('inputmoney')
        inputmoney.value = ''
      },
      inputchange(e){
        this.money = parseInt(e.target.value)
      }
    }
  }
</script>

<style lang="less" scoped>
  .Sentiment{
    height: 12.667vw;
    line-height: 12.667vw;
    :nth-child(1){
      vertical-align: -0.833vw;
      color: #f00;
      font-size: 7.222vw;
      padding: 0 1.389vw;
    }
    :nth-child(2){
      color: rgb(204, 197, 115);
      font-size:  3.856vw;
    }
  }
  .moneylist{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    font-size: 4vw;
    padding: 0 1.389vw 5.556vw;
    li{
      width:23vw;
      height: 8.333vw;
      line-height: 8.333vw;
      text-align: center;
      border-radius: 2.778vw;
      border: 0.278vw solid rgb(204, 197, 115);
      color:rgb(204, 197, 115);
      margin-top: 2.778vw;
      transition: all 0.3s linear;
      &:hover{
        background-color: rgb(204, 197, 115);
        color: #fff;
      }
    }
  }
  .othermoney{
    display: flex;
    justify-content: center;
    color:rgb(204, 197, 115);
