<template>
  <div class="home">
    <div class="swipper">
      <swipper />
      <div class="TempleName">{{TempleName}}</div>
    </div>
    <description />
    <homemiddle ref = 'homemiddle'/>
    <div class="footer">
      <div @click="$router.push('profile')"><van-icon name="arrow-left" />我的功德</div>
      <div @click="$router.push('detaildonation')">查看功德<van-icon name="arrow" /></div>
    </div>
    <div class='submitbtn' @click="submitbtnclick">捐献功德</div>
  </div>
</template>

<script>
import swipper from "./swipper"
import description from "./description"
import homemiddle from "./homemiddle"
export default {
  name: 'Home',
  data(){
    return {
      TempleName : '东华寺中殿',
    }
  },
  components: {
   swipper,
   description,
   homemiddle,
  },
  methods: {
    submitbtnclick(){
      if(this.$refs.homemiddle.money == 0){
        this.$msg.fail('请选择金额！')
      }else{
        const money = this.$refs.homemiddle.money
        const data = {'金额': money}
        console.log(data)
        this.$msg.success('您已成功捐献'+money+'元！')
        setTimeout(()=>{
          this.$router.push('/profile')
        },10)
      }
    }
  }
}
</script>
